import axios from 'axios';
import React, { Component } from 'react';

const CLIENT_ID = '86fbsr0u4omral';
const CLIENT_SECRET = 'nRUqlOlPccd29pux';
const REDIRECT_URI = 'http://localhost:3000';
const CORS_PROXY = 'https://cors-anywhere.herokuapp.com/';

class LinkedInLogin extends Component {
  constructor() {
    super();
    this.state = {
      authCode: '',
      accessToken: '',
      profile: {},
    };
  }

  componentDidMount() {
    const urlParams = new URLSearchParams(window.location.search);
    const authCode = urlParams.get('code');

    if (authCode) {
      this.setState({ authCode }, () => {
        this.getAccessToken();
      });
    }
  }

  getAccessToken() {
    axios
      .post(`${CORS_PROXY}https://www.linkedin.com/oauth/v2/accessToken`, null, {
        params: {
          grant_type: 'authorization_code',
          code: this.state.authCode,
          redirect_uri: REDIRECT_URI,
          client_id: CLIENT_ID,
          client_secret: CLIENT_SECRET,
        },
        headers: {
          'orgin': REDIRECT_URI,
        }
      })
      .then((response) => {
        this.setState({ accessToken: response.data.access_token }, () => {
          this.getUserProfile();
        });
      })
      .catch((error) => {
        console.error('Error getting access token:', error);
      });
  }

  getUserProfile() {
    axios
      .get(`${CORS_PROXY}https://api.linkedin.com/v2/me`, {
        headers: {
          Authorization: `Bearer ${this.state.accessToken}`,
        },
      })
      .then((response) => {
        this.setState({ profile: response.data });
      })
      .catch((error) => {
        console.error('Error getting user profile:', error);
      });
  }

  handleLoginClick() {
    const url = `https://www.linkedin.com/oauth/v2/authorization?response_type=code&client_id=${CLIENT_ID}&redirect_uri=${encodeURIComponent(
      REDIRECT_URI
    )}&scope=r_liteprofile%20r_emailaddress`;

    window.location.href = url;
  }

  render() {
    return (
      <div>
        <button onClick={this.handleLoginClick.bind(this)}>
          Login with LinkedIn
        </button>
        {this.state.profile && (
          <div>
            <h3>User Profile:</h3>
            <pre>{JSON.stringify(this.state.profile, null, 2)}</pre>
          </div>
        )}
      </div>
    );
  }
}

export default LinkedInLogin;
