import './App.css';

import React, { useState } from 'react';

import axios from 'axios';

import AudioRecorder from './AudioRecorder';
import LinkedInLogin from './Login';

function App() {
  const [coverLetter, setCoverLetter] = useState("")

  function handleSubmit(e) {
    e.preventDefault();

    // read the form data
    const form = e.target;
    const formData = new FormData(form)
    console.log(`Submit resume ${formData.get('resume')} and job post ${formData.get('jobpost')} for customization`)
    axios.post('http://localhost:8080/resume', {
      resume: formData.get('resume'),
      jobpost: formData.get('jobpost')
    })
      .then(function (response) {
        console.log(response)
        setCoverLetter(response['data']['message'])
      })
  }

  return (
    <div className="App">
      <header className="App-header">
        {/* <img src={logo} className="App-logo" alt="logo" /> */}
        <p>
          <LinkedInLogin />
        </p>
        <p>
          <AudioRecorder />
        </p>
        <p>
          JobBot
        </p>
        <p>
          <form method="post" onSubmit={handleSubmit}>
            <label for="resume"> Resume:
              <textarea type="text" id="resume" name="resume"></textarea>
            </label>

            <label for="jobpost"> Job post link:
              <input type="text" id="jobpost" name="jobpost"></input>
            </label>
            <button
              type='submit'
            >
              Generate cover letter
            </button>
          </form>
        </p>
        <p>
          <h1>Cover letter</h1>
          <p>
            {coverLetter}
          </p>
        </p>

      </header>
    </div>
  );
}

export default App;
