import React, { useState } from 'react';

const AudioRecorder = () => {
  const [recording, setRecording] = useState(false);
  const [mediaRecorder, setMediaRecorder] = useState(null);
  const [audioChunks, setAudioChunks] = useState([]);
  const [audioURL, setAudioURL] = useState('');

  const handleStartRecording = async () => {
    // get audio stream from user's microphone
    const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
    // create a MediaRecorder instance to capture audio stream
    const recorder = new MediaRecorder(stream, { type: 'audio/webm' });
    setMediaRecorder(recorder);

    recorder.addEventListener('dataavailable', (e) => {
      // recorder.ondataavailable = (e) => {
      console.log(`${e.data.size} bytes of data available`)
      setAudioChunks((prev) => {
        return [...prev, e.data]
        // prev.push(e.data);
        // return prev;
      });
    });

    recorder.start()
    setRecording(true);
  };

  const handleStopRecording = () => {
    if (mediaRecorder) {
      mediaRecorder.stop();

      const blob = new Blob(audioChunks, { type: 'audio/webm' });
      setAudioURL(window.URL.createObjectURL(blob));
      setAudioChunks([]);
    }
  };

  return (
    <div>
      <button onClick={handleStartRecording} disabled={recording}>
        Start Recording
      </button>
      <button onClick={handleStopRecording} disabled={!recording}>
        Stop Recording
      </button>
      {audioURL && (
        <div>
          <h3>Recorded Audio:</h3>
          <audio src={audioURL} controls='controls' />
          <a href={audioURL} download='audio.webm'>Download </a>
        </div>
      )}
    </div>
  );
};

export default AudioRecorder;
